/* eslint-disable */
import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/components/Index';
import CivilSMS from '@/components/CivilSMS';
import OuterSMS from '@/components/OuterSMS';
import EmailPush from '@/components/EmailPush';
Vue.use (Router);

export default new Router ({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path: '/civilSMS',
      name: 'civilSMS',
      component: CivilSMS,
    },
    {
      path: '/outer-sms',
      name: 'outerSMS',
      key: 'OuterSMS',
      component: OuterSMS,
    },
    {
      path: '/email-push',
      name: 'emailPush',
      key: 'EmailPush',
      component: EmailPush,
    },
  ],
});



