<template>
  <div>
    <h1>内部短信推送</h1>
    <hr>
    <div class="row at-row flex-center">
      <div id="add" class="col-md-18">
        <div class="flex-start">
          <br>
          <h2>短信模板</h2><br>
          <at-card v-if="templateContent != ''" :no-hover="true">
            <div style="min-height:30px">
              {{ templateContent }}
            </div>
          </at-card>

          <ul id="formUl">
            <li>
              <label class="demo-desc">短信模板</label>
              <at-select v-model="SMSTemplateChosen" @on-change="updateTemplate" size="large" style="width:auto;max-width:200px;min-width:50%">
                <at-option :value="item.id" :key="item.id" v-for="item in SMSTemplates">{{item.text}}</at-option>
              </at-select>
            </li>
            <li v-for="item in fillIns" :key="item.id">
              <label class="demo-desc">字段{{"{"}}{{item.id}}{{"}"}}</label>
              <at-input v-model="item.text" placeholder="输入提示" style="width:auto;max-width:60%;min-width:50%"></at-input>
            </li>

            <br>
            <h2>发送范围</h2>
            <li>
              <label class="demo-desc">组别列表</label>
              <at-select v-model="groupChosen" size="large" multiple style="width:auto;max-width:60%;min-width:50%">
                <at-option v-if="item.id<12" :value="item.id" :key="item.id" v-for="item in groupList">{{item.name}}</at-option>
              </at-select>
            </li>

            <li>
              <label class="demo-desc">队员姓名</label>
              <at-select v-model="memberChosen" size="large" multiple style="width:auto;max-width:60%;min-width:50%">
                <at-option :value="item.userid" :key="item.userid" v-for="item in memberList">{{item.name}}</at-option>
              </at-select>
            </li>
          </ul>

          <at-button :loading="submitLoading" id="submit" type="primary" v-on:click="submit">提交</at-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
const service = axios.create({
  baseURL: process.env.API_HOST,
  timeout: 5000,
  withCredentials: true
})
export default {
  name: 'civilSMS',
  methods: {
    updateTemplate: function () {
      if (this.SMSTemplates.length > 0) {
        this.SMSTemplates.map(function (n) {
          if (n.id === this.SMSTemplateChosen) {
            this.templateContent = n.text
          }
        }.bind(this))
      }
      this.fillIns = []
      /* eslint-disable */
      this.fillIns = this.templateContent.match(/[^{\}]+(?=})/g).map(function (e) { return { id: e, text: '' } })

    },
    submit: function () {
      console.log(this.fillIns)
      this.$Loading.start()
      this.submitLoading = true
      service
        .post( '/open/sms',
          {
            template: this.SMSTemplateChosen,
            param_list: this.fillIns.map(function (n) {
              return n.text
            }),
            group_chosen: this.groupChosen,
            member_chosen: this.memberChosen
          }
        )
        .then(function (response) {
          this.$Message.success('发送成功')
        }.bind(this))
        .catch(function (error) {
          this.$Loading.error()
          this.$Message.error(error.response.data.msg)
        }.bind(this))
        .then(function () {
          this.$Loading.finish()
          this.submitLoading = false
        }.bind(this)
        )
    }
  },

  data () {
    return {
      groupChosen: [ ],
      memberChosen: [ ],
      SMSTemplateChosen: 0,
      groupList: [ ],
      memberList: [ ],
      fillIns: [ ],
      SMSTemplates: [ ],
      templateContent: '',
      submitLoading: false
    }
  },
  mounted: function () {
    service
      .get('/weixin/department', { timeout: 4000 })
      .then(function (response) {
        this.groupList = response.data.data
      }.bind(this))
      .catch(function (error) {
        this.$Loading.error()
        this.$Message.error(error.response.data.msg)
      }.bind(this))
      .then(function () {
        this.$Loading.finish()
      }.bind(this)
      )
    service
      .get('/weixin/department/1', { timeout: 4000 })
      .then(function (response) {
        this.memberList = response.data.data
      }.bind(this))
      .catch(function (error) {
        this.$Loading.error()
        this.$Message.error(error.response.data.msg)
      }.bind(this))
      .then(function () {
        this.$Loading.finish()
      }.bind(this)
      )
    service
      .get('/message/sms/template', { timeout: 4000 })
      .then(function (response) {
        this.SMSTemplates = response.data.data
      }.bind(this))
      .catch(function (error) {
        this.$Loading.error()
        this.$Message.error(error.response.data.msg)
      }.bind(this))
      .then(function () {
        this.$Loading.finish()
      }.bind(this)
      )
  }
}
</script>

<style>
.at-select {
  padding-left: 20px;
}
.at-input {
  padding-left: 20px;
  display: inline-block;
}
li > label {
  text-align: right;
  width: 100px;
  float: left;
}
#formUl > li {
  line-height: 32px;
}
#add {
  width: 100%;
}
#formUl > li {
  margin: 20px 0;
}
</style>



