<template>
  <div class="row at-row flex-center">
    <div id="add" class="col-md-12">
      <at-alert message="请在 URL 生成器中确认被添加车站不存在" type="info"></at-alert>
      <br>
      <div class="flex-start">
        <p class="demo-desc">车站名称</p>
        <at-input class="at-box-row bg-c-brand-dark" v-model="stationName" placeholder="车站名称"></at-input><br>
        <p class="demo-desc">大区总监</p>
        <at-input v-model="districtManager" placeholder="姓名"></at-input><br>
        <p class="demo-desc">操作人(进行此绑定操作的人姓名)</p>
        <at-input v-model="bussinessManager" placeholder="姓名"></at-input><br>
        <p class="demo-desc">车站公众号</p>
        <at-input v-model="mediaPlatformName" placeholder="公众号名称"></at-input><br>
        <at-checkbox v-model="HaveSameMediaPlateform" label="same-mp-wechat">是否与其他车站共用公众号</at-checkbox><br>
        <br>
        <div v-if="HaveSameMediaPlateform">
          <p class="demo-desc">共用公众号的车站</p>
          <at-select filterable v-model="sameStationName" style="width:140px">
            <at-option :value="item.name" :key="item.order" v-for="item in stationList">{{ item.name }}</at-option>
          </at-select><br><br>
        </div>
        <at-button :loading="submitLoading" class="col-md-4" id="submit" type="primary" v-on:click="submit">提交</at-button>
      </div>
    </div>
  </div>
</template>

<script>
var root = process.env.API_HOST
const axios = require('axios')
export default {
  name: 'Url',
  methods: {
    submit: function () {
      this.$Loading.start()
      this.submitLoading = true
      axios
        .post(root + '/ctrip/station/',
          {
            timeout: 4000,
            stationName: this.stationName,
            districtManager: this.districtManager,
            bussiness: this.bussinessManager,
            mediaPlatformName: this.mediaPlatformName,
            sameStationName: this.sameStationName,
            haveSameMediaPlateform: this.HaveSameMediaPlateform
          }
        )
        .then(function (response) {
          this.$Message.success('成功新增车站')
        }.bind(this))
        .catch(function (error) {
          this.$Loading.error()
          this.$Message.error(error.response.data.msg)
        }.bind(this))
        .then(function () {
          this.$Loading.finish()
          this.submitLoading = false
        }.bind(this)
        )
    }
  },

  data () {
    return {
      HaveSameMediaPlateform: false,
      stationName: '',
      mediaPlatformName: '',
      districtManager: '',
      bussinessManager: '',
      sameStationName: '',
      stationList: [],
      submitLoading: false
    }
  },
  mounted: function () {
    axios
      .get(root + '/ctrip/station/list/', { timeout: 4000 })
      .then(function (response) {
        this.stationList = response.data.data
      }.bind(this))
      .catch(function (error) {
        this.$Loading.error()
        this.$Message.error(error.response.data.msg)
      }.bind(this))
      .then(function () {
        this.$Loading.finish()
        this.downloadFileLoading = false
      }.bind(this))
  }
}
</script>

<style>
</style>



