<template>
    <div>
        <h1>网站索引</h1>
        <hr>
        <div class="row at-row flex-between">
            <at-card @click="jumpTo(item.url)" style="width: 300px;" v-for="item in urlList" :key="item.name" :body-style="{ padding: 0 }">
                <div>
                    <iframe style="width: 100%" frameborder="no" :src="item.url"></iframe>
                    <div style="padding: 14px;">
                        <a :href="item.url" style="color: #2C405A;">{{item.name}}</a>
                    </div>
                </div>
            </at-card>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Url',
  methods: {
    jumpTo (e) {
      window.location.href = e
    }
  },

  data () {
    return {
      urlList: [
        {
          name: '团队官网',
          url: 'https://www.hustunique.com',
          online: true
        },
        {
          name: '团队 BBS',
          url: 'https://bbs.hustunique.com',
          online: true
        },
        {
          name: '公众号',
          url: 'https://mp.weixin.qq.com/',
          online: true
        },
        {
          name: '企业微信',
          url: 'https://work.weixin.qq.com/',
          online: true
        },
        {
          name: 'Family Tree',
          url: 'https://fmt.hustunique.com/',
          online: true
        },
        {
          name: 'Unique Hackday',
          url: 'https://hack.hustunique.com/',
          online: true
        }
      ]
    }
  }
}
</script>

<style>
.at-card {
  margin: 10px;
}
</style>



