              <i class="icon icon-home"></i>消息通知</template>
            <at-menu-item-group title="短信">
              <at-menu-item name="civil-sms" :to="{ name: 'civilSMS' }" replace>内部通知</at-menu-item>
              <at-menu-item name="outer-sms" :to="{ name: 'outerSMS' }" replace disabled>外部消息</at-menu-item>
